<template>
  <div class="dor flex-row-between">
    
  </div>
  
</template>

<script>
import { mapActions } from 'vuex'
import PRESALE_ABI from '@/api/abis/presale.json'
import { utils } from 'ethers'
import { getBit, Tips } from '../../libs/utils'

export default {
    data() {
        return {
            utils,
            getBit,
            isLogin: false,
            availableNum: 1,
            contract: null,
            gasLimit: '1000000',
            // 是否登录
            loading: false,
            // 当前用户是否是白名单
            isWhiteList: false,
        }
    },
    mounted() {
        if (window.wallet.accounts[0] && window.wallet.connectState == 3) {
            this.isLogin = true    
        }
        this.isLogin && this.loginCallBack()
        window.wallet.on('accountChange', () => {
            if (window.wallet.accounts[0] && window.wallet.connectState == 3) {
                this.isLogin = true
            } else { 
                this.isLogin = false
            }
            this.isLogin && this.loginCallBack()
        })
    },
    methods: {
        ...mapActions(['setConnectDialog']),
        loginCallBack() {
            this.contract = window.wallet.initContract("0x829e67f327b7283C44734518C7E83b135a703b74", PRESALE_ABI)
            this.isWhiteListFn().then(() => {
                console.log(this.isWhiteList)
            })
            this.getCoinDetail()
        },
        // 判断当前用户是否是白名单
        async isWhiteListFn() {
            try {
               //获取白名单数量
                const num = await this.contract.getNumberOfWhitelistedUsers()
                const zero = utils.parseUnits('0', 18)
                // 
                if(num.gt(zero)){
                    const list = await this.contract.getWhitelistedUsers(zero, num)
                    const bool = list.find(item => {
                        return item === window.wallet.accounts[0]
                    })
                    this.isWhiteList = !!bool
                } else {
                    this.isWhiteList = false
                } 
            } catch (error) {
                Tips(error)
            }
            
        },
        // 获取代币信息
        async getCoinDetail() {
            try {
                const data = await Promise.all([
                    this.contract.poolSettings(),
                    this.contract.poolStates(),
                    this.contract.getContributionSettings()
                ])
                console.log(data)
            } catch (error) {
                Tips(error)
            }
        },
        // 购买代币
        buyConfirm() {
            this.contract.contribute({
                value: utils.parseUnits("0.1", 18),
                gasPrice: utils.parseUnits("5", 9),
                gasLimit: this.gasLimit
            })
        },
        // 领币
        async claimHandler() {
            try {
                await this.contract.claim();    
            } catch (error) {
                Tips(error)
            }
        },
        // 获取可领取数量
        async getAvailableQuantity() {
            return await this.contract.claimedOf(window.wallet.accounts[0])
        },
    }
}
</script>

<style lang="scss" scoped>
.dor{
    width: 600px;
    max-width: 100%;
    margin: 50px auto;
    .dor-btn{
        height: 50px;
        background: #FCBE2F;
        border: 1px solid #2A2A2A;
        border-radius: 8px;
        width: 100px;
        color: #fff;
        &.is-disabled{
        background: #e4e7ed;
        }
    }
}
.buy{
    width: 80px;
    .buy-icon{
        width: 25px;
        height: 25px;
    }
}
</style>